<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/program')">프로그램</li>
			<li @click="goto('/admin/goods')" class="current">굿즈</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">굿즈 등록</p>
			<div class="input-container marB-40">
				<section class="marB-40">
					<p class="body3 marB-10">대표 이미지</p>
					<div class="input-item align-top">
						<AddImageSingle :refName="'mainImage'" @mainImage="setMainImage" />
					</div>
				</section>

				<section class="basic-info">
					<p class="body3 marB-20">기본 정보</p>

					<div class="input-item">
						<p class="small-title">굿즈명</p>
						<input
							type="text"
							class="admin-input medium"
							name="contents"
							v-model="name"
						/>
					</div>
					<div class="input-item">
						<p class="small-title">가격</p>
						<input
							type="text"
							class="admin-input medium"
							name="contents"
							v-model="price"
						/>
					</div>
				</section>

				<section class="options">
					<p class="body3 marB-20">옵션</p>
					<div
						v-for="(option, index) in options"
						:key="index"
						class="input-item marB-20"
					>
						<p class="small-title">옵션 {{ index + 1 }}</p>

						<div class="input-item-body">
							<input
								type="text"
								class="admin-input medium"
								v-model="option.colorAndSize"
								placeholder="옵션명"
							/>
							<input
								type="text"
								class="admin-input small"
								v-model="option.inventory"
								placeholder="재고"
							/>
							<input
								type="text"
								class="admin-input small"
								v-model="option.extraPrice"
								placeholder="추가가격"
							/>
							<div @click="removeField(index)">
								<CloseRed />
							</div>
						</div>
					</div>

					<button
						class="solid-btn green marB-30"
						@click="addField"
						type="button"
					>
						옵션 추가
					</button>
				</section>
				<LoadingSpinner :loading="isLoading" />
				<section class="marB-40">
					<div class="img-upload-container">
						<p class="body3">상세 이미지</p>
						<div class="input-item align-top">
							<AddImageSingle
								:refName="'detailImage'"
								@detailImage="setDetailImage"
							/>
						</div>
					</div>
				</section>
				<button class="outlined-btn" @click="addGoods">등록</button>
			</div>
		</div>
	</div>
</template>

<script>
import AddImageSingle from '@/components/common/AddImageSingle';
import Store from '@/store';
import { mapGetters } from 'vuex';
import CloseRed from '@/assets/icon/program_goods/CloseRed';
import { addGoods } from '@/api/goods';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { AddImageSingle, CloseRed, LoadingSpinner },
	computed: {
		...mapGetters(['main', 'detail']),
	},
	created() {
		Store.commit('SET_MAIN', null);
		Store.commit('SET_DETAIL', null);
	},
	data() {
		return {
			name: '',
			price: '',
			options: [
				{
					colorAndSize: '',
					inventory: 0,
					extraPrice: 0,
				},
			],
			isLoading: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		addField() {
			this.options.push({
				colorAndSize: '',
				inventory: '',
				extraPrice: '',
			});
		},
		removeField(index) {
			this.options.splice(index, 1);
		},
		async addGoods() {
			let data = new FormData();
			if (this.main) {
				data.append('thumbnailFile', this.main);
			}
			if (this.detail) {
				data.append('detailFile', this.detail);
			}
			data.append('name', this.name);
			data.append('price', this.price);
			data.append('optionList', JSON.stringify(this.options));
			this.isLoading = true;
			let result;
			try {
				result = await addGoods(data);
				if (result.status == 200) {
					alert('상품이 등록되었습니다');
					await this.initForm();
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			} finally {
				this.isLoading = false;
			}
		},
		initForm() {
			this.name = '';
			this.price = '';
			this.options = [{ colorAndSize: '', inventory: 0, extraPrice: 0 }];
			Store.commit('SET_MAIN', '');
			Store.commit('SET_DETAIL', '');
		},
		setMainImage(data) {
			Store.commit('SET_MAIN', data);
		},
		setDetailImage(data) {
			Store.commit('SET_DETAIL', data);
		},
	},
};
</script>

<style scoped>
.basic-info {
	margin-bottom: 40px;
}
.input-item-body {
	display: flex;
	align-items: center;
}
.options {
	margin-bottom: 40px;
}
</style>
